.banner {
  position: relative;
  height: 20vh;
  background: black;
  color: white;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.title {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 3rem;
  font-weight: 100;
}

.small {
  position: absolute;
  top: -1rem;
  left: 0;
  font-size: 1rem;
  font-weight: normal;
}

@media (min-width: 40rem) {
  .banner {
    height: 32vh;
  }

  .title {
    font-size: 6rem;
  }
}



